<template>
  <div class="todos">
    <h1 class="todos__title">Todos</h1>
    <div class="todos__block">
      <div class="todos__item" v-for="todo in todos" :key="todo.id">
        <p class="todos__todo-title">{{ todo.title }}</p>
        <p class="todos__body">{{ todo.completed }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["todos"]),
  },
};
</script>
